@import url(https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&display=swap);
.App {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    background: #ADA996;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #EAEAEA, #DBDBDB, #F2F2F2, #ADA996); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-family: 'Arvo', serif;
}

.App h1 {
    font-size: 2rem;
    margin-top: -0.5em;
    color: rgb(34, 31, 31);
    text-align: center;
}

#content-container {
    margin: 0;
    padding: 1rem;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

#selector-wrapper {
    margin: 0;
    padding: 0;
}

#workspace-wrapper {
    display: flex;
    flex-direction: column;
    padding: 1.5em;
    margin: 0;
    height: 100%;
}

#viewer {
  height: 30%;
  max-height: 40vh;
  min-height: 150px;
  padding: 0.5em;
  border-radius: 0.5em 0.5em 0 0;
  outline: none;
}

#editor{
    height: 100%;
    min-height: 200px;
    margin-top: 1em;
    padding: 0.5em;
    border-radius: 0.5em 0.5em 0 0;
    outline: none;
}

.dropdown-toggle {
    margin-bottom: 0.5em;
    box-sizing: border-box;
    width: 95%;
    text-align: left;
    overflow: hidden;
}

.dropdown-toggle::after {
    display: none;
}

.dropdown-menu {
    width: 95%;
    text-align: left;
    overflow: hidden;
    border: solid 1px;
}

.copy-button {
    margin: -1px 0;
    border-radius: 0 0 0.5em 0.5em; 
}

